
export default [
   `Но эта информация была настолько секретна, что ее могли знать только самые надежные люди. Не то чтобы синьор Рамеро не доверял этим двоим - ставка была слишком высока. Он не имел права рисковать. И потому поделился со своими гостями лишь частью информации.`,
   `Он сидел боком на стуле, свесив правую руку через его спинку, а левой придерживая сползший плащ. В этой же левой руке его дымилась особенная плоская папироса с золотом на том конце, который кладут в рот, и ее дым, задевая мое лицо, пахнул, как хорошая помада.`,
   `Старый Спенсер опять начал качать головой. И при этом ковырял в носу. Он старался делать вид, будто потирает нос, но на самом деле он весь палец туда запустил. Наверно, он думал, что это можно, потому что, кроме меня, никого тут не было.`,
   `Его лук был известен во всем войске, и не только в войске, но и на всем протяжении реки от моря до порогов. Я сам сделал для него этот лук, когда ему перестали нравиться те маленькие луки и стрелы, с которыми ему приходилось иметь дело.`,
   `Нельзя так спрашивать, она прекрасно это знает, и во всех книгах по психологии написано, что нельзя спрашивать мужчину о его личной жизни и нельзя задерживать его, когда он уходит. Нельзя, потому что можно утратить чувство собственного достоинства.`,
   `Я стал вспоминать, кто нравится мне. Мне нравятся собаки, потому что они всегда любят одного и того же человека. Твоей матери нравятся кошки, потому что они знают, чего хотят. Мне кажется, что будь кошки вдвое крупнее, их, скорей всего, запретили бы держать дома.`,
   `Он затаил дыхание и осторожно повернул голову к ветви. Смятение охватило его. Всего один раз она говорила с ним, в день избрания. Тогда она только назвала его имя; тогда она всех назвала по имени и больше с ними не говорила. Никогда.`,
   `Оба господина, чья жизнь была трудна, грабили Хаселе и других ловцов животных до такой степени, до которой те позволяли себя грабить. Причем они знали, что и их тоже будут надувать и грабить, когда они будут менять полученные шкуры на другие товары.`,
   `Вокруг царила мертвая тишина. Только легкая поземка, которую гнал по тоненькой корочке наста ветер, едва слышно шуршала возле уха. Казалось, Марсал находился совершенно один в холодном, застывшем мире, где не было и не могло быть места ни для чего живого.`,
   `А потом она опять забралась в кресло, прихватив с собой шерсть и котенка, и снова принялась за клубок. Но дело у Алисы шло медленно, потому что она все время отвлекалась - то беседовала с Китти, а то бормотала что-то себе под нос.`,
   `Пока он говорил, подошла карета скорой помощи. Она двинулась на нас задним ходом. Из нее выскочили санитары, открыли дверь, вытащили длинные носилки и бросились в пункт первой помощи. Доктор пошел следом за ними. Вскоре они опять появились, неся Билла на носилках.`,
   `И в самом деле, объявивши о своем уходе, он весь тот день оставался дома, да и ночевал дома тоже. Но на следующее утро он вскочил ни свет, ни заря и, как всегда, - когда созывал людей на охоту, стал дуть в валторну(так он именовал свой охотничий рожок).`,
   `Доркас лежала так тихо, что я было подумал, не спит ли она. Но ее глаза, огромные, небесной синевы глаза, были открыты, и, когда я склонился над ней, они ожили и посмотрели на меня, как смотрели бы, наверное, на расходящиеся по воде круги.`,
   `Через пять минут, за которые переполошился весь двор, на кухню прибежал злой родитель и высказал дочке все, что думает по поводу женщин за рулем. Марго сначала не поняла его претензий, в данном случае она находилась не за рулем, а за книгой.`,
   `И одновременно с этим он превратился в великое Ничто - он не знал, кто он такой, что представляет собой его тело, каковы его расположение и протяженность в пространстве. Он стал всей Вселенной и в то же самое время перестал быть собой.`,
   `Все ученики как один - с ровными зубами, одеты с иголочки, а от их разговоров скулы сводит. В классе меня, конечно, не любили. Я окружил себя высокой стеной, за которую никого не пускал, да и сам старался за нее не высовываться. Такие люди никому не нравятся.`,
   `Он ее не остановил. Плакать Лера начала дома, когда уже объявила родителям, что снова будет жить с ними, когда разложила по шкафам вещи и заперлась в своей комнате на задвижку. Она плакала и пыталась понять, что сделала не так. Выходило, что все так.`,
   `За окнами шел дождь. Небо, одинаково серое, низкое, нависло над Зельгой. Море тоже было серое, но темнее, чем небо. Пристани из окон взгляд не доставал, мешали невысокие деревца на склоне, но мачты новых шхун и трех чужих кораблей виделись ясно.`,
   `Там не было раковин и унитазов потому, что в Делейне не слыхивали о таких вещах - но, если действовать осторожно, можно было накачать воду из колонки высотой в полпальца. Там была рабочая комната с прялкой и ткацким станком, и они тоже работали.`,
   `Последнее время он много пил. Слишком много. Если бы это было в моих силах, я приказал бы замку убрать вино со стола, хотя бы на то время, пока Дроган придет в себя. Но замок не выполняет моих приказов. . . Да, Дроган прав. Я не хозяин здесь, я пленник, которому оставлена лишь иллюзия свободы.`,
   `Надо срочно удирать. Верховный Маг Гессена все еще не врубился, что за мной надо посылать не столько разных демонов, сколько просто одного, но достаточно умелого и сильного. А он полагает, что если, например, у меня есть амулет против каменного демона, то нужно просто прислать водяного.`,
   `За утешением и советом Омелько приходил обычно к деду. Тот жил в балке, в стороне от села. Выбираться к нему было не так-то просто еще и потому, что дед и отец не дружили. Омелько не знал почему. И не расспрашивал особенно - стерегся.`,
   `До сих пор его бросает в жар от стыда за содеянное! И ведь не ребенок уже был, в институте учился! Отец к тому времени приходил к ним домой крайне редко, они с Михаилом встречались на стороне. Он, конечно, давал деньги на жизнь, но для себя мама ничего не просила.`,
   `Таита изучал их всю жизнь. Ему казалось, он знает и понимает все, что можно знать и понимать касательно звезд, но теперь с помощью своего Внутреннего Ока он получал новое представление об их свойствах и роли в извечном устройстве мироздания и в делах людей и богов.`,
   `На металлической двери с внутренней стороны не было ручки. Эмили не знала, сколько времени она просидела в этой комнате с голыми стенами и гладкой, как стекло, дверью, с раковиной и унитазом в одном углу и кроватью - в другом. Ей казалось, что она здесь очень давно.`,
   `И внезапно она поняла, что же было не так все это время. Теперь она знала, как можно было бы сделать свой мир добрым и счастливым местом. На этот раз все было верно, все могло бы получиться, и никого не нужно было бы ни к чему прибивать.`,
   `На самом деле не так важно, как и почему эта хитрая методика работает, гораздо важнее то, что она работает! Так что отбросьте свой скепсис. Ведь вы наверняка не знаете, как устроен ваш мобильный телефон, но это не мешает вам им пользоваться.`,
   `Это всегда неожиданно и страшно. Никаких сенсаций или обвинений от меня не последует. Если кто и убийца, так это та жизнь, которая часто не по делу мучила, терзала, отнимала силы и здоровье и за которую он в один прекрасный день перестал бороться.`,
   `Он сидел в первом ряду, стараясь смотреть на сцену, потому что знал - сотни человек смотрят на него сейчас и будут смотреть позже. Он не оборачивался, но сознание того, что он в центре внимания, не покидало его. У него были карие, живые и умные глаза.`,
   `Он кивнул, словно ответ его удовлетворил, сделав все ясным и понятным и не оставив места домыслам. Цири с трудом поднялась, даже не пытаясь наклониться за седлом и упряжью. Только проверила, есть ли у кобылы сено и овес в колоде, а потом принялась протирать спину и бока лошади пучком соломы.`,
   `О, тогда он еще не думал о пенсии. Конец его карьеры, конец его жизни казались ему фантастически далекими, такими далекими, что он не забивал себе голову мыслями об этом. А теперь он уже приобрел дом, в котором проведет остаток дней.`,
   `Роланд сел на постели и увидел свои ноги. На нем не было ничего, кроме туники из прекрасного красного хлопка - одежды, которая как нельзя лучше защищает тело в жарком влажном климате. Старые его вещи, в которые он был одет двадцать один год назад, когда отдавал свой дар, давно истлели.`,
   `Я просто дивился на себя. Вообще-то женщины - мое слабое место, мое, если так можно выразиться, слепое пятно. Однако меня никогда не тянуло на зрелых матрон. Но в этой было нечто такое, что полностью завладело мной. И она это знала.`,
   `Ясно, как день, что если бы он занимался военным делом как ремеслом и хотел нажиться этим путем, он, хозяин стольких провинций, не просил бы разрешения вернуться домой и стеречь свои поля; каждый день наместничества приносил бы ему гораздо больше, чем стоило все его имущество.`,
   `После деда я одна не замыкалась от леса крепким засовом. Было дело: мать в сердцах настегала меня, голенастую шестилетку, за праздность, за то, что весь день ловила в озере раков. Добыча вполне годилась в горшок и, наверное, погодя как раз там и оказалась, но мне уже не было дела.`,
   `Впрочем, времени у Инны было не так уж много. И шевелить ногами следовало быстро. Нужно сбежать по ступеням вниз за то время, пока Бритый будет подниматься на лифте, дойдет до квартиры, увидит, что жены нет, и спустится за ней обратно вниз к машине.`,
   `Я спал в комнате, о которой упоминал, что ее стена, обращенная к морю, была, по существу, огромным окном. Оно шло от потолочного карниза до рамы в полу, а по сторонам на фут не достигало стен. Его створки можно было раздвинуть так, что стекла скрывались.`,
   `Он потом часто вспоминал этот рассказ и потихоньку завидовал. А один раз Алешке даже приснилось что-то похожее. Не совсем похожее, но тоже самолет в поле. Над полем висела теплая ночь с большими звездами, и только у самого горизонта светилась закатная полоса.`,
   `Он разжал пальцы и пошел назад, на другую сторону улицы. Старик посмотрел на часы. За это время он успел немного утомиться. Если обойти квартал, минуя старый дом Вероники, и вернуться к себе, он еще успеет приготовить чай перед тем, как лечь спать.`,
   `Лавров приехал в эти края несколько лет назад, но до сих пор не пропало в нем чувство новизны. Странное волнение охватывало его при виде снежных заносов, летних дождей и туманов, при виде влажных сопок или прозрачных горных ручьев.`,
   `Цыганова так и видела, как эта волна не спеша накатывала на берег и убегала обратно. Но с каждым разом вода все ближе и ближе подбиралась к лежащей на берегу Тамарке. А ей было лень встать и отойти подальше. Даже рукой шевельнуть неохота.`,
   `Прошу меня простить за столь подробное описание псовой охоты, - обратился граф Элим к дамам, прерывая рассказ, - но это поможет вам понять, каким образом я очутился в полном одиночестве и почему за этим последовали столь странные события.`,
   `Деймон положил трубку. Если повезет, подумал он, то, прежде чем закончить книгу, Женевьева испечет не менее сотни пирогов, а к тому времени он уйдет на покой и будет тихо жить в маленьком домике на берегу пролива. Что же, по крайней мере до нее Заловски не добрался, подумал он, положив трубку.`,
   `Семен продолжал ехать вперед, словно ничего не знал и не видел. Наемники от него не отставали. Демоны оказались опытными бойцами: они не проехали сквозь людей, как это делалось обычно, а стали огибать с двух сторон, следуя друг за другом с интервалом в два метра.`,
   `Едва я выпил свою рюмку, в дверь тихо постучали, и вошел Стив Лукас. Каждый раз, глядя на него, я едва удерживался от смеха - он как две капли воды походил на игроков, которых мы видим в каждом вестерне. У него черные как смоль волосы и тонкие усики.`,
   `Фома улёгся на свою кровать. Свою, потому что в любом месте он сразу оказывался как дома. Только вот в своём собственном доме он быть теперь никак не мог. Там жила только Вика, одна маленькая Вика. Может быть, сейчас она плакала, и ей было страшно.`,
   `Стараясь ни о чем не думать, я медленно ехал вдоль проспекта. Надо же, сейчас конец августа, а на улице уже быстро темнеет. В июне в Москве стояли почти белые ночи, но не успел пролететь месяц, как сумерки стали наползать на столицу ранним вечером.`,
   `Вся беда этой женщины заключалась в том, что она не умела радоваться любви. Она умела только страдать. И если жизнь доставляла ей слишком мало страданий(а между тем жизнь никогда не была к ней ласкова и добра, только и знала, что терзала ее да мучила!), она умела создавать себе страдания сама.`,
   `Второй очень странный случай произошел на другой день около половины третьего, когда мама была на работе. Обычно по расписанию, которое Сережа сам для себя составил, он занимался с десяти утра до двух дня, а потом с четырех дня до половины седьмого вечера.`,
   `С каждым ее словом он все больше сознавал свое счастье. И женщина эта казалась ему такой доброй, что он в конце концов все рассказал ей, утаив только свое настоящее имя и побег из тюрьмы. Маркитантка была удивлена и ничего не понимала в том, что ей наговорил этот юный красавчик солдат.`,
   `Я до сих пор не знаю, достаточно хорошая ли она дочь, была ли примерной женой, когда была замужем и являлась ли кроткой невесткой, но одно я знаю точно: Лелька - настоящий друг. Такой, какого можно встретить раз в жизни. Ну, или два, не больше.`,
   `Ему было далеко за пятьдесят, с женой они прожили тридцать лет, но детей у них не было; мысль о появлении в доме мальчишки, который мог оказаться сорванцом, его совсем не радовала. Да и жена брата никогда ему особенно не нравилась.`,
   `О, к тому времени о ролях она знала многое. Все ее семь лет прошли на подмостках театра. Она видела, как мать репетировала, как готовилась к роли, как неуловимо менялась в каждом новом образе. Соня тоже решила играть. Та наивная глупенькая девочка умерла вместе с матерью.`,
   `Иной род вымер сам, иной был изгнан или лишен воды и пищи, сам стал рабом рабов. Оставив под рукой лишь верных, тех, кто боготворил его, Моисея, он повел их в Палестину, поскольку слышал от придворных мудрецов фараона, что эта страна и есть Распутье.`,
   `Вы можете сесть в «Газель» и уехать в Австралию с человеком, который на день приехал на выставку кошек, а можно никуда не ездить, жить на материке вдали от цунами и диких зверей, в нейтральной стране с высоким уровнем жизни, и отравиться шоколадом в кафе, где двести лет не было ни одного микроба.`,
   `И в какой-т  миг ей начинало казаться, что сонный шелест леса переносит ее в совершенно другой мир, не имеющий ничего общего с надоевшим городом. В мир, в котором асфальт так и никогда не появится, а дымом тянуло лишь от редких костров.`,
   `Если вы решили отправиться в лес не только для того, чтобы наполнить свои легкие чистым лесным воздухом и отдохнуть на природе, но еще и для того, чтобы принести домой лесные дары, например ягоды, то вам нужно знать несколько правил по их сбору.`,
   `Молодая Царица скоро почувствовала, что Ей не удалось пробудить к себе искренних симпатий в новом отечестве, по крайней мере, среди сановных и светских кругов столицы. Она стала еще нервнее и еще более замкнулась в семейной жизни, страдая от того, что Ее добрые намерения не поняты и не оценены.`,
   `Порой Сакамото казалось, что русские вообще лишены чувства времени. Если на работу отводилось три дня, они два из них ничего не делали, зато за последний оставшийся день успевали сделать все, что нужно. Причем в самом лучшем виде, так, что не придерешься.`,
   `Лишь в углу, держась за кресло, стояла женщина. Роберт потянул было ее к выбитому окну, но та лишь еще сильнее вцепилась в подлокотник. Оставив ее, Роберт двинулся по вагону. Жена и двое детей сидели почти в середине. Но сейчас он не мог даже определить середину вагона.`,
   `Морра раскинула руки и побежала по болоту, дико хохоча и поднимая веера брызг. Перед ней падали ниц камыши, и стелилась осока. Дух зла был на свободе. Вдруг Морра резко остановилась и сжала кулаки. Как жаль, что она бессильна в свете дня.`,
   `Что уж говорить обо мне. Я привстал на четвереньки и потряс головой, стряхивая с каски песок. Сплюнув несколько раз, я очистил рот от песка. Если не считать того, что голова у меня раскалывалась от зверской боли, в остальном я был в полном порядке.`,
   `Мне никто не ответил. Возможно, она работала теперь по круглосуточному графику и у нее как раз выпала дневная смена, и может быть, она просто принимала душ или пошла в магазин. Я решил, что проще всего прямо заехать к ней домой, тем более, что это было совсем рядом.`,
   `Это очень странный остров. Он тянется в длину мили на три и состоит почти что из одного морского песка. Ширина его нигде не превышает четверти мили. От материка он отделен едва заметным проливом, вода в котором с трудом пробивает себе путь сквозь тину и густой камыш - убежище болотных курочек.`,
   `В старину с морковью было связано немало поверий. Ее считали лакомством гномов и думали, что если в лесу на ночь оставить миску с пареной морковью, то на следующий день можно найти на этом месте миску с золотом: гномы, обрадованные угощением, щедро отплатят тем, кто его оставил.`,
   `Кажется, я всегда попадаю в Италию только в мертвый сезон. В августе и сентябре разве что остановлюсь проездом дня на два, чтобы еще разок взглянуть на милые мне по старой памяти места или картины. В эту пору очень жарко, и жители Вечного города весь день слоняются взад и вперед по Корсо.`,
   `Старый и больной До Тран, прикованный к креслу на колесах, вообще выкатился из комнаты. Очевидно, желая таким образом показать, что не хочет помешать ей каким бы то ни было образом. Все всегда хотели, чтобы Гота оказалась где угодно, только не здесь.`,
   `Людям жалко было своих близких, угодивших в пекло. Вот они и просили попов и монахов молиться за них. А поскольку правильно молиться никто не умел, то и молитвы были самодельные. Вот когда обретут странники в чёрных рясах правильного бога, тогда и молитвы будут правильные.`,
   `А вот Филя, хоть и пытался держать себя в руках, расклеился основательно. Придя домой, он уже целый час слонялся из комнаты в комнату, и вовсе не читал никаких книг, и не собирался в дорогу. Все валилось у него из рук. Особенно когда он смотрел в печальные Дуськины глаза.`,
   `Мне всегда было обидно, что на нем нет шрамов, если не считать одного между ребер в правой стороне. Да и на шрам - то не походила красная полоса, скорее, на зажившую царапину. Однако и такого шрама никто не видел, отец не любил говорить о своей ране, тем более никому не показывал.`,
   `Ей приснился сон, даже не сон, это была лишь короткая вспышка, видение, образ огромной совы в темном дупле. Она снялась со своего прежнего гнезда и теперь пряталась в тени. Серо - белый узор на ее крыльях был похож на мертвые листья, налепленные прямо поверх костей.`,
   `Я обошел все берега залива и нанес их на карту. Северный берег крут и обрывист и состоит из засоленной глины и белого гипса. Ни травы, ни деревьев нет. Вдоль восточного берега возвышаются унылые горы, а южный берег низок и покрыт множеством соляных озер.`,
   `Олег встал у края дороги и, подняв руку, почти сразу остановил одну из машин. Он еще ни разу не ездил по Москве на наземном транспорте, да и пешком практически не ходил. Город для него был и оставался набором разрозненных пейзажей, видимых от станций метро.`,
   `Потому что Кирена была рядом с ним с самого момента их первой встречи два дня назад. Конечно, не в прямом смысле слова, но, несомненно, она преследовала его, и Голт не был уверен, что он сможет долго сопротивляться этому настойчивому желанию.`,
   `Он рассказал мне, что другой товарищ уже извозчик, что они оба следят за домом министра и что однажды им удалось увидеть его карету. Он тут же описал мне внешний вид выезда Плеве: вороные кони, кучер с медалями на груди, ливрейный лакей на козлах и сзади - охрана: двое сыщиков на вороном рысаке.`,
   `И тут краем глаза Антон Момулович заметил одного очень неприятного для него гражданина в военной форме, который собирался, ни больше ни меньше, как набить А. М. морду за то, что тот приставал на одной вечеринке к жене военного - даме очень крупных габаритов ниже талии.`,
   `А она хотела войти в чащу, и тогда царь спустил за ней сокола, и сокол бил её крыльями по глазам, пока не ослепил и не ошеломил. И царь вынул дубинку и ударил газель и повалил её, и потом он сошёл и прирезал газель и, сняв с неё шкуру, привесил её к луке седла.`,
   `В этот день ей исполнялось сорок шесть лет. Не так уж много для женщины, совсем не старушечий возраст. Но то, что верно для женщины, не было верно для Старухи, потому что Старуха была старой девой. А девы становятся старухами и в тридцать, не говоря уже о сорока шести.`,
   `Она сидела в постели, укутавшись в покрывало, грея руки о кружку с чаем, и наблюдала за тем, как Мозес хлопочет в палатке. Он вылил ведро горячей воды в умывальник и повесил рядом чистое белое полотенце. Потом налил кипяченой воды в стакан для полоскания зубов и выдавил на щетку немного пасты.`,
   `По крайней мере, голова хоть не так болит, как утром. На протяжении всего дня каждое усилие давалось ему с невероятным трудом, и он боялся, что придется приступать к делу, а пурпурные точки будут все так же мелькать перед глазами.`,
   `По словам женщины, ни одного доброго слова об отце и матери она от него не слышала. Более того, она поведала потрясенным следователям, что в день похорон сын снял с лежавшего в гробу отца новый костюм и заменил его на старый. А новый на следующий день отнес в комиссионку.`,
   `Она прекрасно помнит, что любила эту осеннюю ветку именно за то, что она напоминала ей о первой, хлопающей по стеклу дома, в который она уже никогда не вернется и о котором ничего больше не помнит. Теперь она стоит в цветочном магазине на фоне темно - розовых маков на высоких волосатых стеблях.`,
   `И я просидел, беседуя с ним, до ночи, а потом я встал, зажёг большую свечу и заправил светильник, и мы сидели, поставив сначала кое - какую еду. И мы поели, и я встал и поставил сладости, и мы лакомились и сидели, беседуя друг с другом, пока не прошла большая часть ночи.`,
   `Тут мотор стал понемногу подниматься. Куски рельсов были наклонены в сторону двери - совсем немного, но этого хватило, чтобы корпус мотора силой тяги стало тащить вверх. Он сотрясался и от этого подползал все ближе к концам рельсов.`,
   `Тогда глазеть на незнакомца собралось все племя. Среди Камышовых Котов редко кто доживал до старости, чаще всего умирали, не дожив до седых волос. Вначале мужчины наставили на непрошеного гостя копья, но потом, увидев, что он не опасен, опустили их.`,
   `Днем Таита тоже не спал, но сидел в тени в дальнем углу террасы. Сразу после ужина он снова поднялся в башню. Солнце еще виднелось на горизонте, но маг не хотел терять ни мгновения темной поры, когда можно наблюдать звезды. Ночь пришла быстро, крадучись, как вор.`,
   `Тем более что Сеятели явно не похищали Терри. Если бы она применила кольцо под их контролем, я оказался бы не на берегу озера, а в камере, заполненной усыпляющим газом. А то и просто в тисках силового поля. Никакой меч мне бы не помог.`,
   `Уже много лет они не просили Памелу о предсказаниях. Когда они были помоложе и чувствовали себя чужими в большом городе, для них это стало почти ритуалом. Они в шутку просили ее «поворожить» на будущее с новым приятелем или на шансы сделать карьеру на новой работе.`,
   `Нет, про имена она ничего говорить не станет. А вот про остальное! Гутька вообще совести никогда не имела! Пока ее четыре сестры скромно строили глазки местным трактористам, она умотала в город и выскочила замуж за городского Ваську.`,
   `Мама тогда свидетелем проходила по делу против папы и остальных мужиков. Они сидели там, держась за руки, и мама все плакала, плакала, пока из зала суда не вышел охранник в серой форме и не сказал, что осудили их на двадцать один год.`,
   `Два человека в белом возникли в дверном проеме, и Лука махнул им рукой, чтобы входили. Носилки они оставили у парадной двери, как и положено делать в Венеции, а вместо них несли плетеное кресло, которое использовали для переноски больных по узким витым лестницам.`,
   `Некоторые из них стали на постой на стадионе Супербия к востоку от Турая, однако больше ничего нам о них не известно. Амраг вывел войска из поля нашего зрения. Наши маги обшарили волшебным взором всю округу, но колдуны орков тоже не лыком шиты.`,
   `Братья молча лежали в темноте. Рудольф отодвинулся дальше от Тома. Он считал, что неприлично спать в одной кровати с братом. В комнате было холодно. Рудольф всегда распахивал настежь окно по ночам, и с реки долетали холодные порывы холодного ветра.`,
   `Вот уже двадцать лет он работает на Массино. Больше ему не пришлось никого убивать, так как шеф все дело прибрал к своим рукам. Массино контролирует не только синдикат, но и подпольные игорные дома, и никто не может ему противостоять.`,
   `Прочитав эти страницы и открыв для себя Тайну, вы узнаете, как получить или сделать все, что вы хотите, как стать тем, кем вы хотите. Вы придете к пониманию, кто вы есть на самом деле. Вы осознаете истинное величие - оно ждет вас впереди.`,
   `Никто, в общем-т , не просит и не требует у нее мандаринов. Но ведь на день рождения обычно угощают весь класс. Так что Анна бежит по лестнице вниз. Столовая уже пуста. За дверью в кухню тихо. Ящики с мандаринами все так же стоят у стены.`,
   `С этим, конечно, глупо спорить, однако в тот момент меня мало волновало и даже, сказать по чести, мне было безразлично, что они там затевают и как это отразится на мне. Когда человек долгие годы живет в постоянном страхе, он привыкает к нему, как инвалид к коляске.`,
   `Эти ключи Танька дала ей после того, как умер ее отец и Танька стала жить одна. Танина мать ушла из семьи, когда дочке было всего шесть лет или даже того меньше. Во всяком случае, это случилось еще до того, как Танька пошла в школу.`,
   `Но все же он был солдат и глянул на Джека с пониманием и даже с уважением, словно бы написать в штаны - то самое, что по уставу полагается совершить в такой ситуации. Перед тем как покинуть их, он сделал то, что хотел сделать Джек, - отдал ему с мамой честь по всем правилам.`,
   `В более поздние годы я то и дело видел этот дом во сне. Все сны были похожими - вариации одного сна и одной темы. Я иду по незнакомому городу и вижу дом. Он стоит в ряду домов в квартале, которого я не знаю. Я иду дальше, сбитый с толку, потому что знаю дом, но не знаю городского квартала.`,
];